import React from "react";
import c from "./Description.module.css";
import IconButton from "../../core/button/IconButton";
import moment from "moment";

//MUI
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Typography,
  Grid,
} from "@material-ui/core/";
import Skeleton from "@material-ui/lab/Skeleton";

const Description = ({
  post,
  update,
  deleting,
  compare,
  options,
  toggle,
  isSame,
  setSame,
}) => {
  const updatePost = () => {
    toggle();
    update(post);
  };
  const deletePost = () => deleting(post);
  const comparePost = () => compare(post);

  return (
    <Grid item xs={12} sm={6} md={4}>
      <Card className={c.items}>
        <CardHeader
          title={
            options["title"].isChecked && post.title !== "" ? (
              <h4 className={c.items_title}>{post.title}</h4>
            ) : (
              <Skeleton variant='text' />
            )
          }
          subheader={
            options["createAt"].isChecked && post.createAt !== "" ? (
              <div className={c.items_date}>
                <div className={c.date_and_time}>
                  {post.createAt
                    ? moment(post.createAt).format("MMMM Do YYYY, h:mm:ss a")
                    : ""}
                </div>
              </div>
            ) : (
              <Skeleton variant='text' />
            )
          }
        />

        <CardContent>
          <Typography variant='body2' color='textPrimary' component='span'>
            {options["description"].isChecked && post.body !== "" ? (
              <div className={c.items_text}>{post.body}</div>
            ) : (
              <>
                <Skeleton variant='text' />
                <Skeleton variant='text' />
                <Skeleton variant='text' />
                <Skeleton variant='text' />
              </>
            )}
          </Typography>
        </CardContent>
        <CardActions>
          {!isSame ? (
            <div className={c.buttonBlock}>
              <IconButton
                title='Top same'
                onClick={comparePost}
                icon='compare'
              />
              <IconButton title='Edit' onClick={updatePost} icon='edit' />
              <IconButton
                title='Delete'
                className={c.button_delete}
                onClick={deletePost}
                icon='delete'
                color='red'
              />
            </div>
          ) : (
            <IconButton
              title='All posts'
              onClick={() => setSame(false)}
              icon='list'
            />
          )}
        </CardActions>
      </Card>
    </Grid>
  );
};

export default Description;
